<template>
    <div class="about">
        <img :src="baseInfo.banner" class="img"/>
        <div class="navigation">
            <img src="../../assets/home.png" class="home"/>
            <div class="m-l-7 pointer" @click="navigator(baseInfo.home.router)">{{ baseInfo.home.title }}</div>
            <div class="m-l-7"> > </div>
            <div class="m-l-7 pointer" @click="navigator('/job')">{{ baseInfo.title }}</div>
            <div class="m-l-7"> > </div>
            <div class="m-l-7">{{ job[index].job_title }}</div>
        </div>
        <div style="background-color: #fff;">
            <div class="job">{{ $t('jobTitle') }}：【{{ job[index].job_title }}】</div>
        </div>
        <div class="card">
            <div class="describe border">
                <div class="name">{{ $t('jobDescription') }}</div>
                <div class="de-text">
                    <div style="line-height: 26px;" v-for="(item,index) in job[index].job_description" :key="index">
                        {{ index+1 }}. {{ item }}
                    </div>
                </div>
            </div>
            <div class="describe">
                <div class="name">{{ $t('jobRequirements') }}</div>
                <div class="de-text">
                    <div style="line-height: 26px;" v-for="(item,index) in job[index].requirements" :key="index">
                        {{ index+1 }}. {{ item }}
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>
<script setup>
import { ref, computed, onMounted } from "vue";
import { useRoute, useRouter } from 'vue-router';
import { useStore } from 'vuex';

    const store = useStore();
    const route = useRoute();
    const router = useRouter();
    let index = ref(route.query.index ? Number(route.query.index) : 0);
    const job = computed(() => store.state.info.recruitment);

    const baseInfo = computed(() => {
        if (store.state.info.navigation) {
            let obj = store.state.info.navigation.find(el => el.router === '/job')
            document.title = `${ store.state.info.recruitment[route.query.index || 0].job_title }-${ obj.title }-${ store.state.info.company_name }`
            return {
                banner: obj.banner,
                title: obj.title,
                home: store.state.info.navigation[0]
            }
        }
        return {
            banner: '',
            title: '',
            home: {}
        }
    });

    const navigator = (path) => {
        router.push(path)
    }
   
</script>
